"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { buttonVariants } from "../ui/button";

export const HeaderLinks = () => {
  const isNotRoot = usePathname() !== "/";

  return (
    <>
      {isNotRoot && (
        <div className="inspect flex h-14 w-[100%] items-center justify-center gap-2 whitespace-nowrap font-geistmono text-sm">
          {/* <Link
            className={`${buttonVariants({ variant: "default" })} mb-10`}
            href="/route/overview"
          >
            {userInfo?.user ? "Sign out" : "Sign in"}
          </Link> */}
        </div>
      )}
    </>
  );
};
